import React from 'react';
import { Box, Heading, Text } from 'grommet';
import { LightGrey } from './global/colors';
import TechTag from './global/tags/TechTag'

function JobViewExtraInfoSubSection(params) {
    return (
        <Box fill='horizontal' background='#FFFFFF' gridArea='side' pad='small' margin='small'>
            <Heading size='18px' margin="none" color={LightGrey}>WEBSITE</Heading>
            <Box pad="small" direction='row'>
                <a href={params.website}>{params.website}</a>
            </Box>
            <Heading size='18px' margin='none' color={LightGrey}>TECH STACK</Heading>
            <Box align="center" direction='row'>
                {params.techStack && params.techStack.map(text => (
                    <TechTag label={text} />
                ))}
            </Box>
            <Heading size='18px' margin="none" color={LightGrey}>LOCATION</Heading>
            <Box pad="small" direction='row'>
                <Text>{params.location}</Text>
            </Box>
        </Box>
    );
}

export default JobViewExtraInfoSubSection;
