import React from 'react';
import { Box } from 'grommet';

import { CaretNext } from 'grommet-icons';


function Job(params) {
  return (
    <Box fill background='#FFFFFF' gridArea='main' pad='small' margin='small'>
      <h3>Job Responsibilities:</h3>
      <p>
        {params.responsibilities.map(text => (
          <div>
            <CaretNext size='small' /> {text}
          </div>
        ))}
      </p>
      <h3>Required Experience:</h3>
      <p>
        {params.experiences.map(text => (
          <div>
            <CaretNext size='small' /> {text}
          </div>
        ))}
      </p>
    </Box>
  );
}

export default Job;
