import React from 'react';
import PropTypes from "prop-types";
import PostSalaryBenefits from './PostSalaryBenefits'
import PostJobDescription from './PostJobDescription'
import PostPublish from './PostPublish'
import { Box, Tabs, Tab, Grommet } from 'grommet'
import { CustomizeTheme } from './global/styles'
import { Purple } from './global/colors'
import { css } from 'styled-components'

function Post({ checked: checkedProp, ...rest }) {

  const [index, setIndex] = React.useState();
  const onActive = nextIndex => setIndex(nextIndex);

  const theme = {
    tab: {
      active: {
        color: "dark-1"
      },
      color: "accent-1",
      border: {
        color: "accent-1"
      },
      size: '250px',
      extend: ({ theme }) => css`
      font-weight: bold;
    `
    },
    tabs: {
      gap: "medium",
    }
  }


  return (
    <Grommet theme={CustomizeTheme(theme)}>
      <Tabs activeIndex={index} onActive={onActive} justify="left">
        <Tab title={<RichTabTitle
          // icon={<Checkmark color="green" />}
          label='1: Salary and benefits'
        />}>
          <Box fill background='#FFFFFF' gridArea='main' pad='small' margin='small'>
            <PostSalaryBenefits />
          </Box>
        </Tab>
        <Tab title={<RichTabTitle
          label='2: Job Description'
        />}>
          <Box fill background='#FFFFFF' gridArea='main' pad='small' margin='small'>
            <PostJobDescription />
          </Box>
        </Tab>
        <Tab title={<RichTabTitle
          label='3: Publish'
        />}>
          <Box fill background='#FFFFFF' gridArea='main' pad='small' margin='small'>
            <PostPublish />
          </Box>
        </Tab>
      </Tabs>
    </Grommet>
  );
}

export default Post;

const RichTabTitle = ({ icon, label }) => (
  <Box direction="row" align="center" gap="xsmall" width='250px'>
    <span style={{ color: Purple }}>{label}</span>
    {icon}
  </Box>
);

RichTabTitle.propTypes = {
  label: PropTypes.string.isRequired
};